import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import { toast, toastError } from "./swal-config";
import Swal from "sweetalert2";
import slugify from "slugify";
import router from "./router";

Vue.use(Vuex)

const baseURL = '/'
// const baseURL = 'https://localhost:5001/'


let _api = Axios.create({
  baseURL: baseURL + 'api',
  timeout: 5000,
  // withCredentials: true
})

export default new Vuex.Store({
  state: {
    bugs: [],
    notes: [],
    bug: {}
  },
  mutations: {
    setBugs(state, bugs = []) {
      state.bugs = bugs
    },
    setBug(state, bug = {}) {
      state.bug = bug
    },
    addBug(state, bug) {
      if (!bug) { return }
      state.bugs.unshift(bug)
    },
    setNotes(state, notes = []) {
      state.notes = notes
    },
    addNote(state, note = {}) {
      state.notes.unshift(note)
    },
  },
  actions: {
    async getBugs({ commit, state }) {
      try {
        let res = await _api.get("Bugs/")
        commit('setBugs', res.data)
      } catch (err) { toastError(err) }
    },
    async createBug({ dispatch, commit }) {
      Swal.mixin({
        input: 'text',
        confirmButtonText: 'Next &rarr;',
        showCancelButton: true,
        progressSteps: ['1', '2']
      }).queue([
        "Provide a short Title for the Bug",
        "Please describe the bug in detail?",
      ]).then((result) => {
        if (!result.value) { throw new Error("Bug Creation Canceled") }
        dispatch('sendBug', result.value)
      }).catch(e => toastError(e))
    },
    async sendBug({ commit }, val) {
      try {
        let bug = {
          title: val[0],
          description: val[1],
        }
        let res = await _api.post("bugs", bug)
        if (res.data.id) {
          toast({ title: "Bug Created" })
          commit("addBug", res.data)
          return true
        }
      } catch (e) { toastError(e) }
    },
    async getBug({ commit, state }, id) {
      try {
        let resId = await _api.get("bugs/" + id)
        if (resId.data) {
          if (resId.data) {
            commit('setBug', resId.data)
          }
        }
      } catch (e) { toastError(e) }
    },
    async saveBug({ dispatch }, bug) {
      try {
        let res = await _api.put("bugs/" + bug.id, bug)
        toast({ title: "Bug Updated" })
        router.push({ name: "bug", params: { slug: bug.slug } })
      } catch (e) { toastError(e) }
    },
    async deleteBlog({ dispatch }, id) {
      try {
        let res = await _api.delete("bugs/" + id)
        toast({ title: "Blog Closed" })
        dispatch('getBugs')
      } catch (e) { toastError(e) }
    },

    async getNotes({ commit }, id) {
      try {
        commit('setNotes', [])
        let resId = await _api.get("bugs/" + id + '/notes')
        if (resId.data) {
          if (resId.data) {
            commit('setNotes', resId.data)
          }
        }
      } catch (e) { toastError(e) }
    },

    async runTests() {
      var bdata = {
        title: "__test__blog",
        description: "__a__test__summary"
      }
      let bugs
      let bug
      let notes


      try {
        toast({ title: "Starting Bug Tests", type: "info" })
        let res = await _api.get("bugs")
        bugs = res.data
        if (!Array.isArray(bugs)) {
          throw new Error("Unable to get bugs or bad data retrived when requesting bugs")
        }
      } catch (e) { return toastError(e) }



      try {
        toast({ title: "Testing Bug Creation", type: "info" })
        let res = await _api.post('bugs', bdata)
        bug = res.data
      } catch (e) { return toastError(e) }

      try {
        let res = await _api.get("bugs/" + bug.id)
        bug = res.data
        if (!bug) { throw new Error("Unable to retrive bug by its id") }
        if (bug.closedDate) { throw new Error("Bug was created with a closedDate") }
      } catch (e) { return toastError(e) }

      try {
        toast({ title: "Testing Bug Edit", type: "info" })
        bug.description = "___THISISATEST___"
        bug.closedDate = new Date(Date.now()).toUTCString()
        let res = await _api.put("bugs/" + bug.id, bug)
        if (res.data.closedDate) { throw new Error("Bugs should not be closable on edits") }
      } catch (e) {
        return toastError(e)
      }

      try {
        toast({ title: "Testing Find BugById", type: "info" })
        let res = await _api.get("bugs/" + bug.id)
        if (!bug.description == res.data.description) {
          throw new Error("Bug was not edited appropriately")
        }
        bug = res.data
      } catch (e) {
        return toastError(e)
      }

      try {
        let deltebug = await _api.delete("bugs/" + bug.id)
        let res = await _api.get("bugs/" + bug.id)
        // res.data.closedDate = " REMOVEME "
        if (!res.data.closedDate) {
          throw new Error("Bug was not closed on delete request")
        }
      } catch (e) {
        return toastError(e)
      }

      toast({ title: "Testing Blog Notes", type: "info" })
      let nbug
      let note1
      try {
        bdata.body = "__TEST__BUG__WITH__NOTES__"
        let bres = await _api.post('bugs', bdata)
        nbug = bres.data
        let cres = await _api.post("bugnotes", {
          body: "THIS__NOTE__IS__GREAT",
          bugId: nbug.id
        })
        note1 = cres.data

        let commentEditRes = await _api.put("bugnotes/" + note1.id, { body: "I__CAN__EDIT__MY__NOTE" })
        if (commentEditRes.data.body != "I__CAN__EDIT__MY__NOTE") {
          throw new Error("Unable to edit Notes")
        }
      } catch (e) {
        return toastError(e)
      }

      try {
        let res = await _api.get("bugs/" + nbug.id + "/notes")
        let notes = res.data
        if (!Array.isArray(notes) || notes.length != 2) {
          throw new Error("notes not returned when requesting blogs/:id/notes")
        }
      } catch (e) {
        return toastError(e)
      }

      toast({ title: "All tests successfully passed Excellent Job!!!", type: "success" })

    }
  }
})
