<template>
  <div>
    <div class="blog-container" :style="{width: !full ? '50rem': '90%'}">
      <div class="blog-header">
        <div class="blog-cover">
          <div class="blog-author">
            <h3>{{blog.title}}</h3>
          </div>
        </div>
      </div>

      <div class="blog-body" :style="{width: full ? '95%': '80%'}">
        <div class="blog-title mt-1">
          <h1>
            <router-link
              class="text-danger"
              v-if="!blog.closedDate"
              :to="{name: 'blog', params:{id: blog.id}}"
            >{{blog.title}}</router-link>
            <span
              v-else
              class="text-secondary muted"
              style="text-decoration:line-through"
            >CLOSED: {{blog.title}}</span>
          </h1>
          <hr v-if="full" />
        </div>
        <div class="blog-summary">
          <p>{{blog.summary}}</p>
          <p v-if="full" v-html="blog.description"></p>
        </div>
      </div>

      <div class="blog-footer" :style="{width: full ? '95%': '80%'}">
        <ul>
          <li v-if="blog.lastModified" class="published-date">{{blog.lastModified | timeago}}</li>
          <li v-if="full" class="published-date bg-dark text-light">
            <img src="../assets/bug-icon.png" height="25" />
            <span class="ml-1">{{blog.title}}</span>
          </li>
        </ul>
      </div>
    </div>
    <div class="blog-container p-2 bg-dark " :style="{width: !full ? '50rem': '90%'}" v-if="full">
      <h3 class="text-light">Notes:</h3>
      <div class="card square p-2" v-for="n in notes" :key="n.id">{{n.body}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BlogCard",
  props: {
    blog: {
      type: Object,
      required: true
    },
    full: { type: Boolean }
  },
  mounted() {
    if (this.full) {
      this.$store.dispatch("getNotes", this.blog.id);
    }
  },
  computed: {
    notes() {
      return this.$store.state.notes;
    }
  },
  filters: {
    timeago(val) {
      try {
        let d = new Date(val);
        return `${d.toLocaleString()}`;
      } catch (e) {
        return "";
      }
    }
  }
};
</script>

<style>
.blog-container {
  background: #fff;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.2) 0 4px 2px -2px;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  margin: 48px auto;
  width: 95%;
}
@media screen and (min-width: 480px) {
  .blog-container {
    width: 28rem;
  }
}
@media screen and (min-width: 767px) {
  .blog-container {
    width: 40rem;
  }
}
@media screen and (min-width: 959px) {
  .blog-container {
    width: 95%;
  }
}

.blog-container a {
  color: var(--main);
  text-decoration: none;
  transition: 0.25s ease;
}
.blog-container a:hover {
  color: var(--secondary);
}

.blog-cover {
  background: url("https://bcw.blob.core.windows.net/public/images/2710049110504262")
    center no-repeat;
  background-size: cover;
  border-radius: 5px 5px 0 0;
  height: 15rem;
  box-shadow: inset rgba(0, 0, 0, 0.2) 0 64px 64px 16px;
}

.blog-author,
.blog-author--no-cover {
  margin: 0 auto;
  padding-top: 0.125rem;
  width: 80%;
  text-shadow: 2px 2px black;
}

.blog-author h3::before,
.blog-author--no-cover h3::before {
  background: url("../assets/bugbox-logo.png");
  background-size: cover;
  border-radius: 50%;
  content: " ";
  display: inline-block;
  height: 32px;
  margin-right: 0.5rem;
  position: relative;
  top: 8px;
  width: 32px;
  background-color: #302e2e;
}

.blog-author h3 {
  color: #fff;
  font-weight: 100;
}

.blog-author--no-cover h3 {
  color: #999999;
  font-weight: 100;
}

.blog-body {
  margin: 0 auto;
  width: 80%;
}

.video-body {
  height: 100%;
  width: 100%;
}

.blog-title h1 a {
  color: #333;
  font-weight: 100;
}

.blog-summary p {
  color: #4d4d4d;
}

.blog-tags ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  padding-left: 0;
}

.blog-tags li + li {
  margin-left: 0.5rem;
}

.blog-tags a {
  border: 1px solid #999999;
  border-radius: 3px;
  color: #999999;
  font-size: 0.75rem;
  height: 1.5rem;
  line-height: 1.5rem;
  letter-spacing: 1px;
  padding: 0 0.5rem;
  text-align: center;
  text-transform: uppercase;
  white-space: nowrap;
  width: 5rem;
}

.blog-tags li a:hover {
  border-color: #fff;
  background-color: var(--secondary);
  color: #fff !important;
}

.blog-footer {
  border-top: 1px solid #e6e6e6;
  margin: 0 auto;
  padding-bottom: 0.125rem;
  padding-top: 0.25rem;
  width: 80%;
}

.blog-footer ul {
  list-style: none;
  display: flex;
  flex: row wrap;
  flex-wrap: wrap;
  justify-content: flex-end;
  padding-left: 0;
}

.blog-footer li {
  color: #999999;
  font-size: 0.75rem;
  letter-spacing: 1px;
  line-height: 1.5rem;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  white-space: nowrap;
  margin: 0.5em;
}
.blog-footer li a {
  color: #999999;
}

.published-date {
  border: 1px solid #999999;
  border-radius: 3px;
  padding: 0 0.5rem;
}

.numero {
  position: relative;
  top: -0.5rem;
}
</style>